<template>
    <div :config="config" :class="'player-collection' + (userProfileId == 'me' ? '' : ' public')" :style="{width: displayWidth, 'min-height': displayHeight, zoom: displayZoom}">
        <h2 v-if="user">{{ title }}</h2>
        <template v-if="activities === null && achievements === null"><LoadingComp></LoadingComp></template>
        <template v-else>
            <template v-if="activities.length > 0">
                <panel v-for="activity in activities" v-if="games[activity.game_id]" :key="activity.client_id" :id="'player-collection-' + activity.game_id" class="game" :config="{width: '100%', height: '30px', bodyHeight: 'inherit'}">
                    <template slot="body">
                        <a :href="urlFor('game-index', {'id': activity.game_id})" :target="isIn3rdParty ? '_blank' : '_self'">
                            <div class="row clickable">
                                <div class="col-md-2 col-sm-2 col-xs-3 col-content icon" @click="goToGame(activity.game_id)">
                                    <img v-if="games[activity.game_id].icon" :src="forceHTTPS(games[activity.game_id].icon)" class="icon game fixed" @error="gameIconError"/>
                                </div>
                                <div class="col-md-10 col-sm-10 col-xs-9 col-content info">
                                    <div class="chevron-action"><font-awesome-icon icon="chevron-right" size="3x" /></div>
                                    <h2>{{ games[activity.game_id].name }} <small><template v-if="['xs', 'xxs'].includes($mq)"><time :date="activity.last_used">{{ $moment.unix(activity.last_used).format('L HH:mm') }}</time></template><template v-else>{{ getTrans('last_used') }} <time :date="activity.last_used">{{ $moment.unix(activity.last_used).format('LLLL') }}</time></template></small></h2>
                                    <template v-if="Object.keys(achievements).length > 0">
                                    <ul class="list-inline achievements" v-if="achievements[activity.game_id] && hasAtLeastOneValidAchievement(achievements[activity.game_id])">
                                        <li v-for="ach in achievements[activity.game_id].slice(0, getMaxAchievements(activity.game_id))" :key="ach.id" v-tooltip="{html: 'achievement_tooltip_' + activity.game_id + '_' + ach.id, positioning: 'bottom'}">
                                            <img :src="forceHTTPS(getAchievementIcon(ach))" class="achievement icon small" :class="getAchievementIconClass(ach)" />
                                            <div :id="'achievement_tooltip_' + activity.game_id + '_' + ach.id" class="tooltip-content" v-html="getAchievementDescription(ach, $moment.unix(ach.awarded_ts).format('LLLL'))"></div>
                                        </li>
                                        <li v-if="getMaxAchievements(activity.game_id) < achievements[activity.game_id].length" class="and-other-achievements">{{ getTransChoice(getLeftAchivements(activity.game_id), 'and_other_achievements', {'%count%': getLeftAchivements(activity.game_id)}) }}</li>
                                    </ul>
                                    </template>
                                </div>
                            </div>
                        </a>
                    </template>
                </panel>
            </template>
            <template v-else>
                <div class="row">
                    <div class="col-md-12">
                        <p><b>{{ getTrans('noplay') }}</b></p>
                    </div>
                </div>
            </template>
        </template>
    </div>
</template>

<script>
import axios from 'axios';
import store from '@/store';
import HTTP from '@/httpapi';
import Games from '@api/games';
import User from '@api/user';
import GenericMixin from '@mixins/generic';
import AchievementMixin from '@mixins/achievements';
import Achievement from '@classes/achievement';

const MAX_ACHIEVEMENT_COUNT = 6;
const MAX_ACHIEVEMENT_COUNT_BIG_SIZE = 10;

export default {
    name: 'user_public_collection',

    mixins: [GenericMixin, AchievementMixin],

    computed: {
        title () {
            if (this.user) {
                return this.getTrans(
                    'user' + (this.publicDisplay ? '' : '_public') + '_collection_title',
                    {'%login%': this.user.login_name}
                );
            }
        },

        displayWidth () {
            return (this.config.width ? this.config.width : '100%');
        },

        displayHeight () {
            return (this.config.height ? this.config.height : '50px');
        },

        userProfileId () {
            return this.config.userProfile.id;
        },

        displayZoom () {
            return (this.config.zoom ? this.config.zoom : '1');
        },

        publicDisplay () {
            return (this.userProfileId === 'me');
        }
    },

    methods: {
        getCollection () {
            const that = this;
            axios.all([
                User.activities(this.userProfileId),
                User.achievements(this.userProfileId),
                Games.all(),
                Games.rankables()
            ]).then(axios.spread(function (acts, achievements, games, rankables) {
                that.rankables = rankables.data.rankingGames;
                that.games = games.data.games;
                // sort achievements by awarded_ts DESC in each game
                let achs = achievements.data.user.achievements;
                Object.keys(achs).forEach((gid) => {
                    achs[gid].sort((a, b) => (a.awarded_ts > b.awarded_ts ? -1 : (a.awarded_ts < b.awarded_ts ? 1 : 0)));
                    achs[gid] = achs[gid].map((el) => new Achievement(el)).filter((el) => !el.isAlwaysHidden());
                });
                that.achievements = achs;
                that.user = acts.data.user;
                if (store.getters.unloggedMode) {
                    that.activities = acts.data.activities;
                } else {
                    that.activities = acts.data.user.userapps;
                }
            }));
        },

        isRankable (gameId) {
            return (this.rankables && this.rankables.findIndex(function (gid) { return gid === gameId; }) >= 0);
        },

        getMaxAchievements (gameId) {
            let c = this.achievements[gameId].length;
            let max = ['xl'].includes(this.$mq) ? MAX_ACHIEVEMENT_COUNT_BIG_SIZE : MAX_ACHIEVEMENT_COUNT;

            return (c > max ? max : c);
        },

        getLeftAchivements (gameId) {
            return this.achievements[gameId].length - this.getMaxAchievements(gameId);
        },
    },

    mounted () {
        HTTP.ifInitialized(this.getCollection);
    },

    data () {
        return {
            games: null,
            activities: null,
            achievements: null,
            rankables: null,
            user: null
        };
    }
};
</script>

<style type="scss" scoped>
@import '~sass/_variables.scss';
@import '~sass/list.scss';
@import '~sass/image.scss';

.player-collection.public {
    h2 {
        color: map-get($asnet-colors, dark-purple);
    }
    .info {
        h2 {
            color: map-get($asnet-colors, light-purple);
        }
    }
}

.player-collection /deep/ {
    .panel.game {
        .panel-body {
            height: inherit;
            padding: 0;
        }

        h2 {
            color: map-get($asnet-colors, dark-purple);
            font-size: 1.5em;
            max-height: 140px;
            margin-top: 0;
        }

        .icon {
            height: 100%;
            margin-bottom: 10px;
            margin-top: 10px;
            max-height: 100px;
            max-width: 100px;
            min-height: 40px;
            min-width: 40px;
            width: 100%;

            &.achievement {
                width: auto;
                min-height: $icon-max-height;
                &.small {
                    height: $icon-max-height;
                    /* width: $icon-max-width; */
                }
                &.ribbon {
                    min-height: $icon-max-height-sm;
                    height: $icon-max-height-sm;
                }
            }
        }

        .list-inline {
            &.achievements {
                li {
                    min-height: 30px;
                    min-width: 30px;
                    max-height: 60px;
                    max-width: 80px;

                    &.and-other-achievements {
                        color: map-get($asnet-colors, light-purple);
                        font-size: 1.7em;
                        vertical-align: middle;
                        width: 100%;
                        white-space: nowrap;
                    }
                }
            }
        }
        a {
            color: #333;
            text-decoration: none;
        }

        .panel-body {
            .row {
                display: flex;
                margin-left: 0;
                margin-right: 0;

                .chevron-action {
                    bottom: -25px;
                }

                &.clickable:hover {
                    background-color: lightgray;
                    cursor: pointer;

                    .fa-chevron-right {
                        color: white;
                    }
                }

                .col-content {
                    padding: 0 10px 0 0;

                    &.icon {
                        text-align: center;
                        vertical-align: middle;
                    }
                }
            }
        }
    }
}
@include media-breakpoint-down(sm) {
    .player-collection.public .panel.game .info h2 {
        font-size: 1em;
    }
}
</style>



